import React, {Component} from 'react';
import { Button, InputItem, WhiteSpace } from 'antd-mobile';
import 'whatwg-fetch';
import fetchJsonp from 'fetch-jsonp';
import './style.less';
import store from '@/redux/store.js';
import { ChangeGardenInfo, ChangeClassList }  from '@/redux/actions/kindergarten-actions';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLogin: false,
      onLogin: false,
      user: '杭州展示园',
      password: '461432'
    }
  }

  componentDidMount() {
    // console.log(store.getState())
  }

  userChange = (val) => {
    this.setState({
      user: val
    })
  }

  passwordChange = (val) => {
    this.setState({
      password: val
    })
  }

  login = (e) => {
    if (e.nativeEvent.keyCode !== 13 && e.type !== 'click') return
    this.setState({
      onLogin: true
    })
    fetch(`http://sail.youjiaoyun.net/v1/external?payload={"Method":"POST","Area":"api","Path":"CheckLogin","FormData":{"uname":"${this.state.user}","pass":"${this.state.password}"}}`)
    .then(res => res.json())
    .then(data => {
      this.setState({
        onLogin: false
      })
      const d = data.data
      store.dispatch(ChangeGardenInfo(d.LoginInfo))
      if(d.ErrorCode === 0) {
        const option = `gardenId=${d.LoginInfo.gardenID}&gradeNum=0`
        fetchJsonp('http://truelove.youjiaoyun.net/areaAngle/queryClassInfoByGrade?' + option, {
          jsonpCallback: 'jsoncallback'
        })
        .then(res => res.json())
        .then(json => {
          store.dispatch(ChangeClassList(json.classInfo))
          console.log(store.getState())
          this.props.history.push({pathname: '/president', query: {fromLogin: true}})
        })
        .catch(err => {
          console.log(err)
        })
      }
    })
    .catch(err => {
      console.log(err)
    })
  }

  render() {
    return (
      <div className="login">
        <img className="login-bg" src={require('@/assets/images/login_bg.png')} alt="bg" />
        <div className="main">
        <div className="box">
          <InputItem onKeyUp={this.login} onChange={this.userChange} placeholder="请输入用户名" defaultValue="杭州展示园" >用户名：</InputItem>
          <InputItem onKeyUp={this.login} onChange={this.passwordChange} type="password" placeholder="****" defaultValue="461432" >密　码：</InputItem>
        </div>
          <WhiteSpace size="xl" />
          <Button onClick={this.login} loading={this.state.onLogin}>{this.state.isLogin ? '已登录': '登录'}</Button>
        </div>
      </div>
    )
  }
}

export default Login;