import React, { Component } from 'react';
import { Icon, NavBar, WhiteSpace, Picker, List } from 'antd-mobile';
import moment from 'moment';
import 'whatwg-fetch'
import 'es6-promise'
import $common from '@/assets/js/common'
import store from '@/redux/store.js';

import './style.less';

class Details extends Component {
  constructor(props) {
    super(props);
    this.state = {
      className: this.props.match.params.className,
      list: [],
      filterList: [],
      pickList: [
        { label: '全部', value: '全部' },
        { label: '未激活', value: '未激活' },
        { label: '已激活', value: '已激活' }
      ],
      pickVal: ['全部']
    }
  }

  componentDidMount() {
    this.getBinds()
  }

  getBinds = () => {
    const option = `gardenId=${store.getState().kindergarten.gardenInfo.gardenID}&classId=${store.getState().kindergarten.classList.filter(p => p.className === this.state.className)[0].classId}`
    fetch('http://sail.youjiaoyun.net/v1/inspector/invoke/bands?' + option)
    .then(res => res.json())
    .then(json => {
      let $arr = []
      for (let p of json) {
        $arr.push({
          name: p.name,
          lastActivated: p.lastActivated,
          activated: p.activated,
          present: p.present,
          leaving: p.leaving
        })
      }
      $arr = $common.initialSort($arr.filter(p => !p.activated), 'name').concat($common.initialSort($arr.filter(p => p.activated), 'name'))
      this.setState({
        list: $arr,
        filterList: [...$arr]
      })
      console.log(this.state.filterList)
    })
  }

  onChange = (val) => {
    let result = []
    switch (val[0]) {
      case '全部':
        result = this.state.list
        break
      case '未激活':
        result = this.state.list.filter((p) => {
          return !p.activated
        })
        break
      case '已激活':
        result = this.state.list.filter((p) => {
          return p.activated
        })
        break
      default:
        return
    }
    this.setState({
      pickVal: val,
      filterList: result
    })
    console.log(this.state.filterList)
  };

  render() {
    return (
      <div className="braceletActivateDetails">
        <NavBar
          mode="light"
          icon={<Icon type="left" />}
          onLeftClick={() => { this.props.history.goBack() }}
          rightContent={
            <Picker
              data={this.state.pickList}
              value={this.state.pickVal}
              cols={1}
              onChange={this.onChange}
            >
              <List.Item className="pick-box" arrow="down"></List.Item>
            </Picker>
          }
        >{this.state.className}手环激活统计</NavBar>
        <WhiteSpace size="md" />
        <h3 className="remark">备注：时间表示该手环当天被激活的时间</h3>
        <table>
          <tbody>
          {this.state.filterList.map((p, i) => (
            <tr key={i}>
              <td>{p.name}</td>
              <td>{p.leaving ? <i style={{backgroundColor: 'red'}}>请假</i> : <i style={{backgroundColor: 'orange'}}>缺勤</i>}</td>
              <td>{p.activated ? moment(p.lastActivated).format('h:mm:ss') : ''}</td>
              <td>{p.activated ? <em style={{color: '#5c94f7'}}>已激活</em> : <em style={{color: '#999'}}>未激活</em>}</td>
            </tr>
          ))}
          </tbody>
        </table>
      </div>
    )
  }
}

export default Details;