import React, {Component} from 'react';
import { NavBar, Icon, WhiteSpace, Calendar, List, Accordion, Modal } from 'antd-mobile';
import fetchJsonp from 'fetch-jsonp';
import $moment from 'moment'
import store from '@/redux/store.js';
import $common from '@/assets/js/common'

import './style.less';

class Sport extends Component {
  constructor(props) {
    super(props);
    this.state = {
      gardenId: store.getState().kindergarten.gardenInfo.gardenID,
      calendarShow: false,
      calendarDate: $moment().format('YYYY-MM-DD'),
      timeList: [],
      grade: [],
      clockShow: false
    }
  }

  componentDidMount() {
    this.getState($moment().format('YYYY-MM-DD'))
  }

  onConfirm = (time) => {
    this.setState({
      calendarShow: false,
      calendarDate: $moment(time).format('YYYY-MM-DD')
    })
    this.getState(time)
  }

  onCancel = () => {
    this.setState({
      calendarShow: false
    })
  }

  getState = (time) => {
    const _time = $moment(time).format('YYYY-MM-DD')
    const option = `gardenId=${this.state.gardenId}&startTime=${_time}&endTime=${_time}&gradeNum=0&classId=0&type=4&signType=0&pageNo=0&pageSize=1000&personId=0`
    fetchJsonp('http://truelove.youjiaoyun.net//bflmark/selectSleepInfo?' + option, {
      jsonpCallback: 'jsoncallback'
    })
    .then(res => res.json())
    .then(res => {
      let timeList = []
      if (!res.timeList[0].gradeNum) {
        for (let p of res.timeList) {
          timeList.push({ name: '', time: [p.startTime + '-' + p.endTime] })
        }
      }
      let gradeList = $common.excludeRepeat(res.timeList, 'gradeNum')
      gradeList = $common.gradeName(gradeList)
      for (let i in gradeList) {
        timeList[i] = { name: gradeList[i].name, time: [] }
        const filter = res.timeList.filter(item => item.gradeNum === gradeList[i].id)
        for (let j in filter) {
          timeList[i].time[j] = filter[j].startTime + '-' + filter[j].endTime
        }
      }
      const d = res.sleepInfo.sort((p1, p2) => p1.gradeNum - p2.gradeNum)
      let $arr = []
      if (!d.length) {
        this.setState({
          timeList,
          grade: $arr
        })
        return false
      }
      let gradeNumList = $common.excludeRepeat(d, 'gradeNum')
      gradeNumList = $common.gradeName(gradeNumList)
      const gardenTree = {}
      for (let p of gradeNumList) {
        const filter = d.filter(item => item.gradeNum === p.id)
        gardenTree[p.name] = $common.excludeRepeat(filter, 'className')
      }
      const keys = Object.keys(gardenTree)
      keys.forEach((key, i) => {
        const resAll = {
          r1: d.filter(p => p.gradeNum === gradeNumList[i].id && p.quality === '强度低'),
          r2: d.filter(p => p.gradeNum === gradeNumList[i].id && p.quality === '适中'),
          r3: d.filter(p => p.gradeNum === gradeNumList[i].id && p.quality === '强度高')
        }
        $arr.push({
          show: false,
          name: key,
          time: [],
          sum: [
            { name: '强度低', value: resAll.r1.length},
            { name: '适中', value: resAll.r2.length},
            { name: '强度高', value: resAll.r3.length}
          ],
          value: []
        })
        if (res.timeList[0].gradeNum) {
          $arr[i].time = timeList.filter(p => p.name === key)[0].time
        } else {
          for (let p of timeList) {
            $arr[i].time.push(p.time[0])
          }
        }
        for (let p of gardenTree[key]) {
          $arr[i].value.push({
            title: p,
            gradeNum: gradeNumList[i].id,
            classId: store.getState().kindergarten.classList.filter(p1 => p1.className === p)[0].classId,
            value: [
              { name: '强度低', value: resAll.r1.filter(q => q.className === p).length},
              { name: '适中', value: resAll.r2.filter(q => q.className === p).length},
              { name: '强度高', value: resAll.r3.filter(q => q.className === p).length}
            ]
          })
        }
      })
      this.setState({
        timeList,
        grade: $arr
      })
    })
  }

  toDetails = (classInfo) => {
    const gradeName = $common.getGradeName(classInfo.gradeNum)
    this.props.history.push({pathname: '/president/sport/details', query: {classInfo: {...classInfo,
      time: this.state.calendarDate,
      gardenId: this.state.gardenId,
      timeList: this.state.timeList.filter(p => p.name === gradeName)[0].time
    }}})
  }

  render() {
    return (
      <div className="sport">
        <NavBar
        mode="light"
        icon={<Icon type="left" />}
        onLeftClick={() => { this.props.history.goBack() }}
        rightContent={
          <List.Item className="pick-box" onClick={() => {this.setState({ calendarShow: true })}} className="pick-box" arrow="down">{this.state.calendarDate}</List.Item>
        }
        >全园运动表现</NavBar>
        <Calendar
          type={'one'}
          visible={this.state.calendarShow}
          onCancel={this.onCancel}
          onConfirm={this.onConfirm}
          defaultTimeValue={new Date()}
          maxDate={new Date()}
        />
        <WhiteSpace size="md" />
        <h3 className="title">
          <em className="iconfont">&#xe807;</em>
          全园运动时间段
          <i className="iconfont" onClick={() => {this.setState({clockShow: true})}}>&#xe67c;</i>
          <Modal
            visible={this.state.clockShow}
            transparent
            maskClosable={false}
            title={
              <table className="d-table clock">
                <tbody>
                  <tr>
                    <th colSpan="2">全园运动时间段</th>
                  </tr>
                  {this.state.timeList.map((p, i) => (
                  <tr key={i}>
                    <td><em className="iconfont">&#xe614;</em>{p.name}</td>
                    <td>
                      {p.time.map((p1, i1) => (
                        <p key={i1}>{p1}</p>
                      ))}
                    </td>
                  </tr>
                  ))}
                </tbody>
              </table>
            }
            footer={[{text: '知道了', onPress: () => {this.setState({clockShow: false})}}]}
          >
          </Modal>
        </h3>
        <Accordion>
          {this.state.grade.map((p, i) => (
            <Accordion.Panel key={i} header={
              <h3>
                {p.name}
                <em style={{color: "#4caf50"}}><span style={{backgroundColor: "#4caf50"}}></span>{p.sum[0].value}</em>
                <em style={{color: "#2196f3"}}><span style={{backgroundColor: "#2196f3"}}></span>{p.sum[1].value}</em>
                <em style={{color: "#e91e63"}}><span style={{backgroundColor: "#e91e63"}}></span>{p.sum[2].value}</em>
              </h3>
            }>
            <List>
              {p.value.map((p1, i1) => (
                  <List.Item key={i1} arrow="horizontal" onClick={() => {this.toDetails(p1)}}>
                    {p1.title}
                    <em style={{color: "#4caf50"}}><span style={{backgroundColor: "#4caf50"}}></span>{p1.value[0].value}</em>
                    <em style={{color: "#2196f3"}}><span style={{backgroundColor: "#2196f3"}}></span>{p1.value[1].value}</em>
                    <em style={{color: "#e91e63"}}><span style={{backgroundColor: "#e91e63"}}></span>{p1.value[2].value}</em>
                  </List.Item>
              ))}
            </List>
            </Accordion.Panel>
          ))}
        </Accordion>
        {this.state.grade.length ? '' : <p className="no-data">暂无数据</p>}
      </div>
    )
  }
}

export default Sport;