import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import AnimateRouter from './animateRouter'

import NotFound404 from '@/page/NotFound404';
import Login from '@/page/Login';
import President from '@/page/president/Home';
// 全园手环激活统计
import BraceletActivate from '@/page/president/BraceletActivate'
import BraceletActivateDetails from '@/page/president/BraceletActivate/Details'
// 全园手环电量统计
import BraceletElectric from '@/page/president/BraceletElectric'
import BraceletElectricDetails from '@/page/president/BraceletElectric/Details'
// 全园体操表现
import Gymnastics from '@/page/president/Gymnastics'
import GymnasticsDetails from '@/page/president/Gymnastics/Details'
// 全园睡眠表现
import Sleep from '@/page/president/Sleep'
import SleepDetails from '@/page/president/Sleep/Details'
// 全园运动表现
import Sport from '@/page/president/Sport'
import SportDetails from '@/page/president/Sport/Details'

import './style.less';

class BasicRoute extends React.Component {
    render() {
        return (
            <AnimateRouter>
                <Route path="/" exact render={() => (
                    <Redirect to="/login" />
                )} />
                <Route exact path="/login" component={Login} />
                <Route exact path="/president" component={President} />
                <Route exact path="/president/braceletActivate" component={BraceletActivate} />
                <Route exact path="/president/braceletActivate/details/:className" component={BraceletActivateDetails} />
                <Route exact path="/president/braceletElectric" component={BraceletElectric} />
                <Route exact path="/president/braceletElectric/details" component={BraceletElectricDetails} />
                <Route exact path="/president/gymnastics" component={Gymnastics} />
                <Route exact path="/president/gymnastics/details" component={GymnasticsDetails} />
                <Route exact path="/president/sleep" component={Sleep} />
                <Route exact path="/president/sleep/details" component={SleepDetails} />
                <Route exact path="/president/sport" component={Sport} />
                <Route exact path="/president/sport/details" component={SportDetails} />
                <Route component={NotFound404} />
            </AnimateRouter>
        )
    }
}


export default BasicRoute;