export const changeGardenInfo = 'changeGardenInfo';
export const changeClassList = 'changeClassList';
export const changeDetailsClassName = 'changeDetailsClassName';

export function ChangeGardenInfo(gardenInfo) {
  return {
    type: changeGardenInfo,
    payload: gardenInfo
  }
}

export function ChangeClassList(classList) {
  return {
    type: changeClassList,
    payload: classList
  }
}

export function ChangeDetailsClassName(detailsClassName) {
  return {
    type: changeDetailsClassName,
    payload: detailsClassName
  }
}
