import React, { Component } from 'react';
import { Icon, NavBar, WhiteSpace, Carousel, WingBlank, Progress } from 'antd-mobile';
import moment from 'moment';
import Pie from '@/components/Pie';
import 'whatwg-fetch'
import 'es6-promise'
import $common from '@/assets/js/common'
import store from '@/redux/store.js';
// import { ChangeDetailsClassName }  from '@/redux/actions/kindergarten-actions';

import './style.less';

class BraceletActivate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pieData: [
        { value: 115, name: '已激活' },
        { value: 310, name: '未激活' }
      ],
      list: [],
      gardenId: store.getState().kindergarten.gardenInfo.gardenID
    }
  }

  componentDidMount() {
    this.getState()
    this.classState()
  }

  getState = () => {
    const option = `gardenId=${this.state.gardenId}`
    fetch('http://sail.youjiaoyun.net/v1/inspector/invoke/unactivated?' + option)
    .then(res => res.json())
    .then(json => {
        this.setState({
          pieData: [
            { value: json.ActivatedCount, name: '已激活' },
            { value: json.UnactivatedCount, name: '未激活' }
          ]
        })
      });
  }

  classState = () => {
    const option = `gardenId=${this.state.gardenId}`
    fetch('http://sail.youjiaoyun.net/v1/inspector/invoke/bands?' + option)
    .then(res => res.json())
    .then(json => {
      const d = json.sort((p1, p2) => p1.gradeNum - p2.gradeNum)
      let $arr = []
      const gardenTree = {}
      let gradeNumList = $common.excludeRepeat(d, 'gradeNum')
      gradeNumList = $common.gradeName(gradeNumList)
      for (let p of gradeNumList) {
        const filter = d.filter(item => item.gradeNum === p.id)
        gardenTree[p.name] = $common.excludeRepeat(filter, 'className')
      }
      const keys = Object.keys(gardenTree)
      keys.forEach((key, i) => {
        $arr.push({
          name: key,
          value: []
        })
        for (let p of gardenTree[key]) {
          const resUnit = {
            r1: d.filter(q => q.className === p && q.activated),
            r2: d.filter(q => q.className === p && !q.activated)
          }
          $arr[i].value.push({
            title: p,
            value: [
              { name: '已激活', value: resUnit.r1.length},
              { name: '未激活', value: resUnit.r2.length}
            ]
          })
        }
      })
      this.setState({
        list: $arr
      })
      // console.log(this.state.list)
    })
  }

  details = (className) => {
    // store.dispatch(ChangeDetailsClassName(className))
    this.props.history.push('/president/braceletActivate/details/' + className)
  }

  render() {
    return (
      <div className="braceletActivate">
        <NavBar
          mode="light"
          icon={<Icon type="left" />}
          onLeftClick={() => { this.props.history.push('/president') }}
        >全园手环激活状态统计</NavBar>
        <WhiteSpace size="md" />
        <h3 className="title border-bottom">手环激活状态<i>{moment().format('MM月DD日')}</i></h3>
        <Pie data={this.state.pieData}></Pie>
        <WhiteSpace size="md" />
        <h3 className="title border-bottom">各班级手环激活统计</h3>
        <WingBlank>
          <Carousel
            autoplay={false}
          >
            {this.state.list.map((p, i) => (
              <div className="percent-box" key={i}>
                <h3>{p.name}</h3>
                {p.value.map((p1, i1) => (
                  <div onClick={() => {this.details(p1.title)}} className="border-bottom main" key={i1}>{p1.title}<i><Progress percent={p1.value[0].value / (p1.value[0].value + p1.value[1].value) * 100} /></i></div>
                ))}
              </div>
            ))}
          </Carousel>
        </WingBlank>
      </div>
    )
  }
}

export default BraceletActivate;