import React, { Component } from 'react';
import { Icon, NavBar, WhiteSpace, Picker, List } from 'antd-mobile';
import 'whatwg-fetch'
import 'es6-promise'
import store from '@/redux/store.js';

import './style.less';

class Details extends Component {
  constructor(props) {
    super(props);
    this.state = {
      className: store.getState().kindergarten.detailsClassName,
      list: [],
      filterList: [],
      pickList: [
        { label: '全部', value: '全部' },
        { label: '电量低', value: '电量低' },
        { label: '电量正常', value: '电量正常' }
      ],
      pickVal: ['全部']
    }
  }

  componentDidMount() {
    this.getBinds()
  }

  getBinds = () => {
    const option = `gardenId=${store.getState().kindergarten.gardenInfo.gardenID}&classId=${store.getState().kindergarten.classList.filter(p => p.className === this.state.className)[0].classId}`
    fetch('http://sail.youjiaoyun.net/v1/signs/invoke/battery?' + option)
    .then(res => res.json())
    .then(json => {
      let $arr = []
      for (let p of json.data) {
        $arr.push({
          name: p.name,
          lastTime: p.lastTime,
          value: p.battery
        })
      }
      $arr = $arr.sort((a, b) => a.value - b.value)
      this.setState({
        list: $arr,
        filterList: [...$arr]
      })
    })
  }

  onChange = (val) => {
    let result = []
    switch (val[0]) {
      case '全部':
        result = this.state.list
        break
      case '电量低':
        result = this.state.list.filter(p => p.value < 20 )
        break
      case '电量正常':
        result = this.state.list.filter(p => p.value >= 20 )
        break
      default:
        return
    }
    this.setState({
      pickVal: val,
      filterList: result
    })
  };

  render() {
    return (
      <div className="braceletElectricDetails">
        <NavBar
          mode="light"
          icon={<Icon type="left" />}
          onLeftClick={() => { this.props.history.goBack() }}
          rightContent={
            <Picker
              data={this.state.pickList}
              value={this.state.pickVal}
              cols={1}
              onChange={this.onChange}
            >
              <List.Item className="pick-box" arrow="down"></List.Item>
            </Picker>
          }
        >{this.state.className}手环电量统计</NavBar>
        <WhiteSpace size="md" />
        <h3 className="remark">备注：时间表示该手环最近一次电量上报的时间</h3>
        <table>
          <tbody>
          {this.state.filterList.map((p, i) => (
            <tr key={i}>
              <td>{p.name}</td>
              <td>{p.lastTime ? p.lastTime : ''}</td>
              <td>{p.value<20 ? <em style={{color: '#05ce56'}}>电量低</em> : <em style={{color: '#5c94f7'}}>电量正常</em>}</td>
            </tr>
          ))}
          </tbody>
        </table>
      </div>
    )
  }
}

export default Details;