import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import store from '@/redux/store.js';
import { AddPathName, DelPathIdx }  from '@/redux/actions/router-actions';

import './style.less';

class BasicRoute extends React.Component {
    render() {
        return (
            <Router>
                <Route render={({ location }) => {
                  const { children } = this.props
                  let classNames = 'slide-left'
                  const { pathname } = location
                  const idx = store.getState().router.pathName.findIndex(p => p === pathname)
                  if (idx < 0) {
                    classNames = 'slide-left'
                    store.dispatch(AddPathName(pathname))
                  } else {
                    classNames = 'slide-right'
                    store.dispatch(DelPathIdx(idx))
                  }
                  return (
                    <TransitionGroup
                      className="router"
                      childFactory={child => {
                          return React.cloneElement(child, {
                              classNames
                          });
                      }}
                    >
                        <CSSTransition
                            key={location.key}
                            timeout={300}
                        >
                            <Switch location={location}>
                              {children}
                            </Switch>
                        </CSSTransition>
                    </TransitionGroup>
                  )
                }}
                />
            </Router>
        )
    }
}

export default BasicRoute;