import React, { Component } from 'react';
import echarts from 'echarts/lib/echarts';
import 'echarts/lib/chart/line';

import './style.less';

class Line extends Component {
  constructor(props) {
    super(props);
    this.state = {}
  }

  componentDidUpdate() {
    this.draw();
  }

  componentDidMount() {
    this.draw();
  }

  draw = () => {
    const data = this.props.data
    const $obj = {
      xData: [],
      sData: []
    }
    for (let p of data) {
      $obj.xData.push(p.step)
      $obj.sData.push(p.score)
    }
    const option = {
      color: ['#3398DB'],
      barMaxWidth: 10,
      xAxis: {
        type: 'category',
        data: $obj.xData,
        axisLine: {
          lineStyle: {
            color: '#ccc'
          }
        }
      },
      yAxis: {
        type: 'value',
        axisLine: {
            lineStyle: {
                color: '#ccc'
            }
        }
      },
      grid: {
        top: '10%',
        left: 0,
        right: '7%',
        bottom: '10%',
        containLabel: true
      },
      series: [{
        type: 'line',
        data: $obj.sData
      }]
    }
    const $chart = echarts.init(this.refs.chart)
    $chart.setOption(option)
  }

  render() {
    return (
      <div className="line">
        <div ref="chart">chart</div>
      </div>
    )
  }
}

export default Line;