import { addPathName, delPathIdx } from '../actions/router-actions';

const initialState = {
  pathName: []
}

export default function (state = initialState, action) {
  switch (action.type) {
    case addPathName: {
      return {
        ...state,
        pathName: [...state.pathName, action.payload]
      }
    }
    case delPathIdx: {
      return {
        ...state,
        pathName: state.pathName.slice(0, action.payload + 1)
      }
    }
    default:
      return state;
  }
}