import React, { Component } from 'react';
import { NavBar, Icon, WhiteSpace, Picker, List, WingBlank, Carousel, Modal } from 'antd-mobile';
import 'whatwg-fetch'
import 'es6-promise'
import fetchJsonp from 'fetch-jsonp';
import $common from '@/assets/js/common'
import Line from '@/components/Line'

import './style.less'

class SleepDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      classInfo: this.props.location.query.classInfo,
      list: [],
      filterList: [],
      pickList: [
        { label: '全部', value: '全部' },
        { label: '睡眠少', value: '睡眠少' },
        { label: '正常', value: '正常' },
        { label: '睡眠多', value: '睡眠多' }
      ],
      pickVal: ['全部'],
      personIdList: {},
      popTitle: '',
      popValue: [{ scores: [{ step: 0, score: 1 }] }],
      popShow: false, 
      selectedIndex: 0
    }
  }

  componentDidMount() {
    this.getDetails()
    console.log(this.state.classInfo)
  }

  getDetails = () => {
    const option = `gardenId=${this.state.classInfo.gardenId}&startTime=${this.state.classInfo.time}&endTime=${this.state.classInfo.time}&gradeNum=${this.state.classInfo.gradeNum}&classId=${this.state.classInfo.classId}&type=1&signType=0&pageNo=0&pageSize=1000&personId=0`
    fetchJsonp('http://truelove.youjiaoyun.net//bflmark/selectSleepInfo?' + option, {
      jsonpCallback: 'jsoncallback'
    })
      .then(res => res.json())
      .then(json => {
        let d = json.sleepInfo
        d = $common.initialSort([...d].filter(p => p.quality === '睡眠少'), 'name').concat($common.initialSort([...d].filter(p => p.quality === '正常'), 'name')).concat($common.initialSort([...d].filter(p => p.quality === '睡眠多'), 'name'))
        const list = [], personIdList = []
        for (let p of d) {
          let value = ''
          switch (p.quality) {
            case '睡眠少':
              value = 'low'
              break
            case '正常':
              value = 'center'
              break
            case '睡眠多':
              value = 'high'
              break
            default:
              return
          }
          list.push({
            name: p.name,
            value: value
          })
          personIdList[p.name] = p.personId
        }
        this.setState({
          list,
          filterList: list,
          personIdList
        })
      })
  }

  onChange = (val) => {
    let result = []
    switch (val[0]) {
      case '全部':
        result = this.state.list
        break
      case '睡眠少':
        result = this.state.list.filter(p => p.value === 'low')
        break
      case '正常':
        result = this.state.list.filter(p => p.value === 'center')
        break
      case '睡眠多':
        result = this.state.list.filter(p => p.value === 'high')
        break
      default:
        return
    }
    this.setState({
      pickVal: val,
      filterList: result
    })
  };

  getCharts = async (name) => {
    const option = `gardenId=${this.state.classInfo.gardenId}&recordTime=${this.state.classInfo.time}&gradeNum=${this.state.classInfo.gradeNum}&classId=${this.state.classInfo.classId}&type=1&personId=${this.state.personIdList[name]}`
    const res = await fetchJsonp('http://truelove.youjiaoyun.net/bflmark/selectXYarray?' + option, {
      jsonpCallback: 'jsoncallback'
    })
    const data = await res.json()
    this.setState({
      popValue: [data.xyArray],
      popShow: true,
      popTitle: name
    })
  }

  render() {
    const Em1 = () => (<em style={{ color: '#05ce56' }}>睡眠少</em>)
    const Em2 = () => (<em style={{ color: '#5c94f7' }}>正常</em>)
    const Em3 = () => (<em style={{ color: '#ff6f27' }}>睡眠多</em>)
    const Em = (props) => {
      let ele = null
      switch (props.type) {
        case 'low':
          ele = <Em1 />
          break
        case 'center':
          ele = <Em2 />
          break
        default:
          ele = <Em3 />
      }
      return ele
    }
    return (
      <div className="sleepDetails d-table">
        <NavBar
          mode="light"
          icon={<Icon type="left" />}
          onLeftClick={() => { this.props.history.goBack() }}
          rightContent={
            <Picker
              data={this.state.pickList}
              value={this.state.pickVal}
              cols={1}
              onChange={this.onChange}
            >
              <List.Item className="pick-box" arrow="down"></List.Item>
            </Picker>
          }
        >{this.state.classInfo.title}睡眠表现明细</NavBar>
        <WhiteSpace size="md" />
        <table>
          <tbody>
            {this.state.filterList.map((p, i) => (
              <tr key={i}>
                <td>{p.name}</td>
                <td><Em type={p.value} /></td>
                <td><em className="iconfont" onClick={() => { this.getCharts(p.name) }}>&#xe67e;</em></td>
              </tr>
            ))}
          </tbody>
        </table>
        <Modal
          visible={this.state.popShow}
          transparent
          onClose={() => {this.setState({popShow: false})}}
          title={
            <div>
              <h3 className="pop-title">{this.state.popTitle}<i className="iconfont">&#xe670;睡眠表现动量柱状图</i></h3>
              <p className="tab-title">
              {this.state.classInfo.timeList.map((p, i) => (
                <i
                  key={i}
                  className={this.state.selectedIndex === i ? 'on' : ''}
                  onClick={() => {this.setState({selectedIndex: i})}}
                >{p}</i>
              ))}
              </p>
              <WingBlank>
                <Carousel
                  autoplay={false}
                  selectedIndex={this.state.selectedIndex}
                  afterChange={(index) => {this.setState({selectedIndex: index})}}
                >
                  {this.state.popValue.map((p, i) => (
                    p.length ? <Line data={p} key={i} /> : <p className="no-data" key={i}>暂无数据</p>
                  ))}
                </Carousel>
              </WingBlank>
            </div>
          }
        >
        </Modal>
      </div>
    )
  }

}

export default SleepDetails;