export default {
  // 杭州碧海银帆科技有限公司
  // 班级等级
  gradeName($arr) {
    const d = []
    for (let p of $arr) {
      switch (p) {
        case 1:
          d.push({ id: 1, name: '托班' })
          break
        case 2:
          d.push({ id: 2, name: '小班' })
          break
        case 3:
          d.push({ id: 3, name: '中班' })
          break
        case 4:
          d.push({ id: 4, name: '大班' })
          break
        case 5:
          d.push({ id: 5, name: '其他班' })
          break
        case 6:
          d.push({ id: 6, name: '毕业班' })
          break
        case 7:
          d.push({ id: 7, name: '蒙班' })
          break
        case 8:
          d.push({ id: 8, name: '学前班' })
          break
        case 9:
          d.push({ id: 9, name: '混龄班' })
          break
        default:
          return
      }
    }
    return d
  },
  // 班级等级
  getGradeName(num) {
    let d = ''
    switch (num) {
      case 1:
        d = '托班'
        break
      case 2:
        d = '小班'
        break
      case 3:
        d = '中班'
        break
      case 4:
        d = '大班'
        break
      case 5:
        d = '其他班'
        break
      case 6:
        d = '毕业班'
        break
      case 7:
        d = '蒙班'
        break
      case 8:
        d = '学前班'
        break
      case 9:
        d = '混龄班'
        break
      default:
        return
    }
    return d
  },
  // 班级等级id
  getGradeNum(name) {
    let d = 0
    switch (name) {
      case '托班':
        d = 1
        break
      case '小班':
        d = 2
        break
      case '中班':
        d = 3
        break
      case '大班':
        d = 4
        break
      case '其他班':
        d = 5
        break
      case '毕业班':
        d = 6
        break
      case '蒙班':
        d = 7
        break
      case '学前班':
        d = 8
        break
      case '混龄班':
        d = 9
        break
      default:
        return
    }
    return d
  },

  ///////////////////////////////////////////////////////////

  // 去重
  excludeRepeat(oldArr, repeatKey) {
    const newArr = []
    for (let i = 0; i < oldArr.length; i++) {
      let flag = true
      for (let j = 0; j < newArr.length; j++) {
        if (oldArr[i][repeatKey] === newArr[j]) {
          flag = false
        }
      }
      if (flag) {
        newArr.push(oldArr[i][repeatKey])
      }
    }
    return newArr
  },
  // 计算所有相同属性数量
  compare($obj, $key) {
    let compareObj = {}
    for (let i = 0, len = $obj.length; i < len; i++) {
      if ($obj[i][$key] !== '') {
        if (compareObj[$obj[i][$key]] === undefined) {
          compareObj[$obj[i][$key]] = 1
        } else {
          compareObj[$obj[i][$key]] = compareObj[$obj[i][$key]] + 1
        }
      }
    }
    return compareObj
  },
  // 排序
  compareSort($arr, key, Asc) {
    // 根据属性排序
    function compareFunc(propertyName) {
      if (Asc) {
        return function (obj1, obj2) {
          var val1, val2
          val1 = obj1[propertyName]
          val2 = obj2[propertyName]
          if (val1 < val2) {
            return -1
          } else if (val1 > val2) {
            return 1
          } else {
            return 0
          }
        }
      } else {
        return function (obj1, obj2) {
          var val1, val2
          val1 = obj1[propertyName]
          val2 = obj2[propertyName]
          if (val1 < val2) {
            return 1
          } else if (val1 > val2) {
            return -1
          } else {
            return 0
          }
        }
      }
    }
    const copy = $arr.slice(0)
    return copy.sort(compareFunc(key))
  },
  // 首字母排序
  initialSort($arr, keyName) {
    return $arr.sort((a, b) => a[keyName].localeCompare(b[keyName], 'zh-Hans-CN', { sensitivity: 'accent' }))
  }
}
