import React, { Component } from 'react';
import echarts from 'echarts/lib/echarts';
import 'echarts/lib/chart/pie';

import './style.less';

class Pie extends Component {
  constructor(props) {
    super(props);
    this.state = {}
  }

  componentDidUpdate() {
    this.draw();
  }

  componentDidMount() {
    this.draw();
  }

  draw = () => {
    const option = {
      series: [
        {
          type: 'pie',
          radius: ['60%', '70%'],
          data: this.props.data,
          label: {
            formatter: '{b}: {c}'
          },
          color: ['#3dce78', '#5c94f7']
        }
      ]
    }
    const $chart = echarts.init(this.refs.chart)
    $chart.setOption(option)
  }

  render() {
    return (
      <div className="pie">
        <div ref="chart">chart</div>
      </div>
    )
  }
}

export default Pie;