import { changeGardenInfo, changeDetailsClassName, changeClassList } from '../actions/kindergarten-actions';

const initialState = {
  gardenInfo: {
    gardenID: 1405,
    isParentGarden: true
  },
  classList: [{ classId: 9893, className: "大一班" }, { classId: 9894, className: "中一班" }, { classId: 9895, className: "小一班" }, { classId: 34658, className: "大四班" }, { classId: 44988, className: "改名了" }, { classId: 75172, className: "小二班" }, { classId: 75173, className: "小三班" }, { classId: 75175, className: "小四班" }, { classId: 75192, className: "小五班" }, { classId: 75193, className: "小六班" }, { classId: 75194, className: "小七班" }, { classId: 75196, className: "小九班" }, { classId: 75197, className: "中二班" }, { classId: 75198, className: "中三班" }, { classId: 76716, className: "大三班" }, { classId: 77266, className: "大二班" }, { classId: 77282, className: "托一班" }, { classId: 77472, className: "小小一班" }, { classId: 77473, className: "小小2班" }, { classId: 77474, className: "小小三班" }, { classId: 77475, className: "小小四班" }, { classId: 77476, className: "小小5班" }, { classId: 77477, className: "小小6班" }
  ],
  detailsClassName: '小一班'
}

export default function (state = initialState, action) {
  switch (action.type) {
    case changeGardenInfo: {
      return {
        ...state,
        gardenInfo: action.payload
      }
    }
    case changeClassList: {
      return {
        ...state,
        classList: action.payload
      }
    }
    case changeDetailsClassName: {
      return {
        ...state,
        detailsClassName: action.payload
      }
    }
    default:
      return state;
  }
}